import React from 'react';
import Friend from './Friend';

const FriendList = ({ friends }) => (
  <ul>
    {friends.map(friend => (
      <li key={friend.id}>
        <Friend
          avatar={friend.avatar}
          name={friend.name}
          tag={friend.tag}
          isOnline={friend.isOnline}
          id={friend.id}
        />
      </li>
    ))}
  </ul>
);

export default FriendList;
